.grade-table {
    border-collapse: collapse;
    width: 100%;
  }

  .grade-table th, .grade-table td {
    border: 1px solid #dddddd;
    text-align: center;
    padding: 8px;
  }
  
  .green-header {
    background-color: #4CAF50; 
    color: white;
  }

  .red-grade {
    color: red;
  }

  .grade-table td input {
    width: 100%;
    box-sizing: border-box;
    text-align: center;
  }

  .grade-table input[type="number"] {
    width: 100%;
    box-sizing: border-box;
    text-align: center;
  }

  .grade-table input[type="number"]::-webkit-inner-spin-button,
.grade-table input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.h1 {
  text-align: center;
}
  